.projects {
    min-height: 100vh;
    background-color: #68de23;
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
}

.mySkillsContainer {
    display: flex;
    justify-content: center;
    flex-direction: column;
    flex-wrap: nowrap;
    /*
    height: 80%;
    */
    width: 80%;
    margin: auto;
}

.mySkills {
    width: 100%;
    /*
    height: 25%;
    */
